.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.box {
    border: 1px solid #ccc;
    border-radius: 15px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around; 
    align-items: center;
    gap: 10px;
    min-height: 300px;
}

.formContent{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 100%;
}

.title {
    font-size: 40px;
    font-weight: bold;
    color: #333;
    text-align: center;
}

.inputContainer{
    display: grid;
    grid-template-columns: 150px 1fr;
    align-items: center;
    gap: 10px;
    width: 100%;
    margin-bottom: 10px;
    position: relative;
}

.inputWrapper{
    position: relative;
    width: 100%;
}

.label {
    font-size: 16px;
    font-weight: bold;
    color: #333;
    text-align: left;
}

.input {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 90%;
}

.button {
    padding: 10px 20px;
    background-color: #52bdf1;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
}

.button:hover {
    background-color: #4ca3d4;
}

.button:active {
    background-color: #3f8ec7;
}

.error {
    color: red;
    font-size: 14px;
    text-align: center;
}

.eyeIcon{
    cursor: pointer;
    position: absolute;
    transform: translatey(-50%);
    right: 10px;
    top: 55%;
    font-size: 18px;
}