.container{
    flex: 1;
    display: flex;
    height: 100%;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 15px;
    width: 100%;
    margin: 1.5%;
}

.tableWrapper{
    max-height: 80vh;
    overflow-y: auto;
    width: 100%;
}

.arrow {
    display: inline-block;
    width: 1em;
    text-align: center;
    margin-left: 0.3em;
    font-size: 0.8em;
    color: #555;
  }

.title{
    font-size: 3.2em;
    text-align: left;
    color: #333;
    margin: 0;
    font-weight: bold;
}


.button {
    background-color: #52bdf1;
    width: auto;
    min-width: 120px;
    border-radius: 5px;
    border: none;
    padding: 10px;
    color: white;
    font-weight:bold ;
    align-self: flex-start;
    font-size: 1.2em;
}

.button:hover {
    background-color: #3a9dd1;
    cursor: pointer;
}

.button:active {
    background-color: #2a7db1;
}

.Tableau {
    width: 100%;
    border-collapse: collapse;
}

.th{
    padding-left: 3.55em;
}

.th:hover{
    background-color: #f0f0f0;
    cursor: pointer;
}

.title_tableau{
    font-size: 1.5em;
    text-align: left;
    color: #333;
    font-weight: bold;
    height: 5vh;
    background-color: #f9f9f9;
    position: sticky;
    top: 0;
}

.td{
    padding-left: 5em;
}

.row{
    font-size: 1.1em;
    text-align: left;
    margin: 20px 0;
    color: #333;
    font-weight: bold;
    height: 5vh;
    background-color: white;
    border-bottom: 1px solid #ccc;
    
}

.row:hover{
    background-color: #f0f0f0;
    cursor: pointer;
}



