.container {
    display: flex;
    height: 100vh;
    flex-direction: column;
    align-items: left;
    text-align: left;
    padding: 15px;
    min-width: 12vw;
    background-color: #f9f9f9;
    overflow-x: scroll;
}

.onglet {
    font-size: 2em;
    text-align: left;
    margin: 20px 0;
    color: #333;
    font-weight: bold;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid #ccc;
    
}

.onglet:hover {
    background-color: #e0e0e0;
    cursor: pointer;
}

.onglet:active {
    background-color: #d0d0d0;
}
