.card {
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 400px;
    width: 100%;
    margin: 2rem auto;
    padding: 2rem;
    background-color: #fdfdfd;
    border-radius: 12px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    overflow: auto;
  }
  
  .title {
    font-size: 2.1rem;
    margin-bottom: 1rem;
    text-align: center;
    color: #333;
    font-weight: bold;
  }

  .container_label_input {
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: flex-start;
    align-items: center;
  }

  .label {
    font-weight: 600;
    margin-right: auto;
  }
  
  .input {
    padding: 0.6rem 1rem;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 1rem;
    transition: border-color 0.2s ease;
    width: 50%;
    align-self: center;
    max-width: 180px;
  }
  
  .input:focus {
    border-color: #52bdf1;
    outline: none;
  }

  .select {
    padding: 0.5rem 1rem;
    border: 1px solid #ccc;
    max-width: 180px;
    border-radius: 6px;
    font-size: 0.8rem;
    transition: border-color 0.2s ease;
    width: 50%;
    align-self: center;
    background-color: white;
  }
  
  .button {
    padding: 0.8rem 1.5rem;
    background-color: #52bdf1;
    color: white;
    border: none;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.2s ease;
    width: 200px;
    align-self: center;
    font-weight: 600;
  }

  .button:hover {
    background-color: #3a9dd1;
  }

  .button:active {
    background-color: #2a7db1;
  }


  .buttonContainer {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    gap: 10px;
    margin-top: 15px;
    align-items: center;

  }

  .fileContainer {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  .input {
    display: none;
  }

  .labelInputContainer {
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
  }

  .fileLabel {
    padding: 0.5rem 0.5rem;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.2s ease;
    width: 100%;
  }

  .fileLabel:hover {
    background-color: #f0f0f0;
  }

  .fileLabel:active {
    background-color: #e0e0e0;
  }