.container {
    position: relative;
    display: flex;

}

.overlay {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    text-align: center;
    justify-content: center;
    align-items: center;
    z-index: 1;
}


.container:hover .overlay{
    display: flex;
    cursor: pointer;
}

.cross {
    background-color: red;
    position: absolute;
    border: none;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    width: 25px;
    height: 25px;
}

.cross:hover {
    background-color: #ac0000;
    cursor: pointer;
}

.cross:active {
    background-color: #660000;
}